import React, { Component } from "react";

class Footer extends Component {
  state = {};
  render() {
    return (
      <div className='footer'>
        <ul className='list-unstyled'>
          <div className='d-inline-block  float-left'>
            <p className='text-white'>&copy; Copyright 2020 Turbo Network * All Right Reserved.</p>
          </div>
          <div className='d-inline-block  float-right'>
            <ul className='list-unstyled'>
              <li className='d-inline-block  footer-icon mx-1'>
                <a href="https://twitter.com/NetworkTurbo" target="blank" className="text-white"><i className='fab fa-twitter'></i></a>
              </li>
              <li className='d-inline-block footer-icon mx-1'>
                <a href="/link" className="text-white"><i className='fas fa-envelope'></i></a>
              </li>
            </ul>
          </div>
          <div className='clearfix'></div>
        </ul>
      </div>
    );
  }
}

export default Footer;
