import React, { Component } from "react";

class Contact extends Component {
  state = {};
  render() {
    return (
      <div className='py-5' id='contact'>
        {/* this section of contact  */}

        <div className='contact'>
          <h2 className='text-capitalize mx-auto mb-5'>
            Go and purchase a copy of Turbo Network as soon as you can!
          </h2>
          <div className='text-center'>
            {/* i disabled navigation here  */}
            <button
              className='btn bg-white font-weight-bold px-3 btn-con btn-m'
              data-toggle='modal'
              data-target='#staticBackdrop'>
              Coming Soon
            </button>
          </div>
        </div>
        <div className='py-4'>
          <p>
            Still struggling or need more information?
            <br />
            Please <b>contact</b> us on Twitter
          </p>
        </div>
      </div>
    );
  }
}

export default Contact;
