import React, { Component } from "react";
/*AOS Library i use it to make animations when scroll to elements*/

import AOS from "aos";
import "aos/dist/aos.css";
/**
 * General component describe Information Component
 */
class Information extends Component {
  state = {};
  /* init OAS */
  constructor(props, context) {
    super(props, context);
    AOS.init();
  }
  componentDidUpdate() {
    AOS.refresh();
  }
  render() {
    return (
      <div className='py-5 text-center'>
        <div
          data-aos='fade-up'
          // this is should be header of landing page
        >
          <h1 className='pt-5 mt-5'>We really are Turbo speed.</h1>

          {/* this is the Description  */}
          <p className='header-p mx-auto py-3'>
            The fasted bot on the market supporting a variety of websites.
          </p>
          <div className='py-2'>
            {/* button that show card login  */}
            <button
              className='btn btn-con bg-white text-black font-weigh-bold  px-4 btn-m'
              data-toggle='modal'
              data-target='#staticBackdrop'>
              Coming Soon
            </button>
          </div>
        </div>
        <div className='py-5'>
          {/* last section of header  */}
          <ul className='text-center list-unstyled'>
            <li className='mx-2 d-inline-block op-sys'>Supporting Operating System</li>
            <li className='mx-2 d-inline-block s-btn'>
              <a href='link' className='text-white'>
                <i className='fab fa-windows btn-i'></i>
              </a>
            </li>
            <li className='mx-2 d-inline-block s-btn'>
              <a href='link' className='text-white'>
                <i className='fab fa-apple btn-i'></i>
              </a>
            </li>
          </ul>
        </div>
      </div>
    );
  }
}

export default Information;
