import React, { Component } from "react";
import AOS from 'aos';
import 'aos/dist/aos.css';
class Description extends Component {
  state = {};
  constructor(props, context) {
    super(props, context);
    AOS.init();
  }

  render() {
    return (
      <div className='ali-h' id='features'>
         {/* Description of features  */}
        <div data-aos='fade-up'>
          <h2 className='wow fadeInUp'>Features</h2>
          <p className='text mx-auto wow animated slideInUp'>
          Turbo Network has been developed so that users have the most optimised and successful bots in the market. See the list of just a few of the various features we support below.
          </p>
        </div>
      </div>
    );
  }
}

export default Description;
