
import $ from "jquery";



$(function () {
  // read more btn in Slider.js Component inside features Component
  $('.tx-cn button').click(function () {

    
    var dots = $(this).closest('p').find('.mrM')
    var moreText = $(this).parent().parent().find('p').find('#dots')
    if(dots.hasClass('more')){
      dots.removeClass('more');
      moreText.text("");
      dots.addClass('less');
      $(this).text("Less")
    } else {
      dots.removeClass('less');
      moreText.text("...");

      dots.addClass('more');
      $(this).text("Read More")
    }
  })

})
