import React, { Component } from "react";

class CardForms extends Component {
  constructor(props) {
    super(props);

    this.state = {
      fields: {},
      errors: {},
      carRun: "",
      btnColor: "",
      text: "Coming Soon",
      carBox: "",
      email: " "
    };
  }

  // make validation function 
  handleValidation() {
    let fields = this.state.fields;
    let errors = {};
    let formIsValid = true;

    //Email
    if (!fields["email"]) {
      formIsValid = false;
      errors["email"] = "Cannot be empty";
    }

    if (typeof fields["email"] !== "undefined") {
      let lastAtPos = fields["email"].lastIndexOf("@");
      let lastDotPos = fields["email"].lastIndexOf(".");

      if (
        !(
          lastAtPos < lastDotPos &&
          lastAtPos > 0 &&
          fields["email"].indexOf("@@") === -1 &&
          lastDotPos > 2 &&
          fields["email"].length - lastDotPos > 2
        )
      ) {
        formIsValid = false;
        errors["email"] = "Email is not valid";
      }
    }

    this.setState({ errors: errors });
    return formIsValid;
  }

  contactSubmit(e) {
    e.preventDefault();
    this.props.onChange();
  }

  // this function reset card section every thing 
  resetCard = () => {
    this.setState({ carRun: " ", btnColor: " ", text: "Coming Soon", carBox: " " });
  };
  handleChange(field, e) {
    let fields = this.state.fields;
    fields[field] = e.target.value;
    this.setState({ fields });
  }
  render() {
    // import props from creditCard component 
    const { onChangeForm, onFocusForm, btnColor, carRun, carBox, text } = this.props;
    return (
      <form className='text-left ani-form' onSubmit={this.contactSubmit.bind(this)}>
        <div className='form-group '>
          <label className='text-white' htmlFor='email'>
            Email *
          </label>
          <div className='d-flex'>
            <i className='fas fa-user-circle  float-left'></i>
            <input
              refs='email'
              id='email'
              type='email'
              name='Email'
              className='form-control float-left'
              placeholder='amandobrownie@email.com'
              onChange={this.handleChange.bind(this, "email")}
              value={this.state.fields["email"] || " "}
              required
            />
          </div>
        </div>
        <div className='form-group'>
          <label className='text-white' htmlFor='number'>
            Card Number *
          </label>
          <div className='d-flex'>
            <i className='fas fa-credit-card'></i>
            <input
              id='number'
              type='number'
              name='number'
              className='form-control'
              placeholder='**** **** **** 3456'
              pattern='[\d| ]{16,22}'
              onChange={onChangeForm}
              onFocus={onFocusForm}
              maxLength={19}
              required
            />
          </div>
        </div>
        <div className='row'>
          <div className='col-6'>
            <div className='form-group'>
              <label className='text-white' htmlFor='datepicker'>
                Expire Date
              </label>
              <div className='d-flex'>
                <i className='far fa-calendar-alt'></i>
                <input
                  id='datepicker'
                  type='text'
                  name='expiry'
                  className='form-control datepicker'
                  placeholder='02/20'
                  required
                />
              </div>
            </div>
          </div>
          <div className='col-6'>
            <div className='form-group'>
              <label className='text-white' htmlFor='cvc'>
                cvv
              </label>
              <div className='d-flex'>
                <i className='fas fa-lock'></i>
                <input
                  id='cvc'
                  type='number'
                  name='cvc'
                  className='form-control'
                  placeholder='12'
                  required
                />
              </div>
            </div>
          </div>
        </div>
        <button className={"btn btn-block btn-lg text-white btn-bu " + btnColor} id='cpa-btn'>
          {text}
        </button>
        <button className='btn btn-block btn-lg text-white'>
          <img
            id='img-car'
            className={carRun + " car-lorry"}
            src={require("../../assets/images/car.png")}
            alt=''
          />
          <img
            id='img-car'
            className={" car-lorry  car-box " + carBox}
            src={require("../../assets/images/box.png")}
            alt=''
          />
        </button>
      </form>
    );
  }
}

export default CardForms;
