import React, { Component } from "react";
/* used AOS library for scroll */
import AOS from "aos";
import "aos/dist/aos.css";
/* Used ReactWOW library to make animation when reach Element */
import ReactWOW from "react-wow";
class ProvenTable extends Component {
  state = {};
  constructor(props, context) {
    super(props, context);
    AOS.init();
  }
  
  render() {
    return (
      <div>
        <div data-aos='fade-up'>
          {/* this is  table of Proven section  */}
          <table className='table table-pro table-borderless table-dark text-left'>
            <thead>
              <tr className='text-uppercase head-tb'>
                <th className="th-radius">Product</th>
                <th>Website</th>
                <th>Price</th>
                <th className="th-radius">Status</th>
              </tr>
            </thead>
            <tbody>
              <ReactWOW animation='slideInRight' delay='5000'>
                <tr className='pyrex'>
                  <td className="th-radius">
                  Nike Blazer Mid Off-White
                    <span className='d-block sm-span'>Black/Large</span>
                  </td>
                  <td className='uni-td'>Allike</td>
                  <td className='uni-td'>$130</td>
                  <td className='text-td uni-td th-radius'>Success</td>
                </tr>
              </ReactWOW>
              <tr>
                <td>
                Adidas Yeezy Boost3...
                  <span className='d-block sm-span'>Black/Large</span>
                </td>
                <td>Titolo</td>
                <td>$220</td>
                <td className='text-td'>Success</td>
              </tr>
              <tr>
                <td>
                Yeezy Boost 350
                  <span className='d-block sm-span'>White/Large</span>
                </td>
                <td>BSTN</td>
                <td>$220</td>
                <td className='text-td'>Success</td>
              </tr>
              <ReactWOW animation='slideInLeft' delay='5000'>
                <tr className='pyrex  pyrex-m '>
                  <td className="th-radius">
                  Air Presto Off-White
                    <span className='d-block sm-span'>Black/Large</span>
                  </td>
                  <td className='uni-td'>Courir</td>
                  <td className='uni-td'>$160</td>
                  <td className='text-td uni-td th-radius'>Success</td>
                </tr>
              </ReactWOW>
              <tr >
                <td>
                Nike React Element
                  <span className='d-block sm-span'>sail light bone</span>
                </td>
                <td> Kickz</td>
                <td> $150</td>
                <td className='text-td'>Success</td>
              </tr>
              <tr>
                <td className="th-radius">
                Air Force 1 Low Travis Scott

                  <span className='d-block sm-span'>Black/Large</span>
                </td>
                <td>SlamJam</td>
                <td>$160</td>
                <td className='text-td th-radius'>Success</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    );
  }
}

export default ProvenTable;
