import React, { Component } from "react";
import ProvenTable from "./dataTable";
import AOS from "aos";
import "aos/dist/aos.css";

class Proven extends Component {
  state = {};
  constructor(props, context) {
    super(props, context);
    AOS.init();
  }
  componentDidUpdate() {
    AOS.refresh();
  }
  render() {
    return (
      <div className='row ali-h' id='proven'>
        {/* this is left side of proven section  */}
        <div className='col-lg-4 text-left d-flex justify-content-center flex-column'>
          <div className='txt-max'>
            <div data-aos='fade-up'>
              {/* title  */}
              <h2>Proven Success</h2>
              {/* descriptions  */}
              <p className='proven-p'>
                With hundreds of checkouts through a limited amount of releases we have shown how
                Turbo Network is an unstoppable force.
              </p>
            </div>
          </div>
        </div>
        <div className='col-lg-1'></div>
        <div className='col-lg-7'>
          {/* this is right side section  */}
          <ProvenTable />
        </div>
      </div>
    );
  }
}

export default Proven;
