import React, { Component } from "react";
import CardForms from "./card-forms";
import Team from "../welcome/team";
class CreditCard extends Component {
  state = {
    cardForm: "",
    teamPic: "",
    carMov: "",
    teamMsg: "",
    bigShow: "",
    showEl: "d-none",
    btnColor: "",
    carRun: "",
    text: "Coming Soon",
    carBox: "",
    email: " ",
    downAnim: "",
    upAnim: ""
  };
  handleInputFocus = e => {
    this.setState({ focus: e.target.name });
  };

  handleInputChange = e => {
    const { name, value } = e.target;

    this.setState({ [name]: value });
  };

  // this function when click on submit button make some animations like indicate below 
  handelForm = () => {
    this.setState({
      cardForm: "card-form",
      teamPic: "team-move",
      carMov: "car-anim",
      teamMsg: "team-msg",
      bigShow: "big-show",
      showEl: " show-logo",
      btnColor: "btn-bl",
      carRun: "car-move",
      text: " ",
      carBox: "car-box-add",
      downAnim: "down-animation",
      upAnim: "up-animation"
    });
  };

  // this function when close module reset every thing 
  resetAll = condition => {
    this.setState({
      cardForm: " ",
      teamPic: "",
      carMov: "",
      teamMsg: " ",
      bigShow: "",
      showEl: "d-none",
      btnColor: " ",
      carRun: " ",
      text: "Coming Soon",
      carBox: " ",
      downAnim: " ",
      upAnim: " "
    });
  };
  render() {
    return (
      <div
        className='modal fade'
        id='staticBackdrop'
        data-backdrop='static'
        tabIndex='-1'
        role='dialog'
        aria-labelledby='staticBackdropLabel'
        aria-hidden={"true"}>
        <div className='modal-dialog' role='document'>
          <div className={"text-left title-turbo " + this.state.showEl}>
            <span className='d-inline-block  p-2'>
              <div className='text-left '>
                <img
                  className='logo'
                  src={require("../../assets/images/c7b63492535aaaac5440a40e89d019e70e3e55b2.png")}
                  alt=''
                />
                <span className='navbar-brand' href=''>
                  <span className='text-white'>Turbo </span>
                  <span className='d-block brand-net  text-white'>Network</span>
                </span>
              </div>
            </span>
          </div>
          <div className='modal-content bg-d-color p-2 px-4'>
            <div className='text-right'>
              <button
                
                type='button'
                className='close text-white'
                data-dismiss='modal'
                aria-label='Close'
                onClick={this.resetAll}>
                <span aria-hidden={"true"}>&times;</span>
              </button>
            </div>

            <img
              className={"my-team " + this.state.teamPic}
              src={require("../../assets/images/card.png")}
              alt='card'
            />
            <img className={'things ' + this.state.downAnim } src={require("../../assets/images/top.png")} alt='' />
            <img
              className={'things things-bot ' + this.state.upAnim}
              src={require("../../assets/images/bottom.png")}
              alt=''
            />
            <img
              className={"my-car " + this.state.carMov}
              src={require("../../assets/images/car.png")}
              alt='card'
            />
            <div className={"big-circ " + this.state.bigShow}></div>
            <div className={"d-flex flex-column justify-content-end go-back " + this.state.teamMsg}>
              <Team />
            </div>
            <div>
              <div className={"  row my-pay " + this.state.cardForm}>
                <div className='offset-1'></div>
                <div className='col-md-10 '>
                  <div className='row'>
                    <div className='col-lg-6 d-flex justify-content-center flex-column'>
                      <div className=''>
                        <div>
                          <h4 className='font-weight-bold text-white py-3 new-h'>
                            Payment Details
                          </h4>
                          <img
                            className='w-100 aw-card aw-md d-none  .d-sm-none d-xl-block d-lg-block  d-md-none'
                            src={require("../../assets/images/waw.png")}
                            alt='card'
                          />
                          <img
                            className='w-100 aw-card aw-md d-sm-block d-xm-block d-md-block d-xl-none d-lg-none'
                            src={require("../../assets/images/waw-2.png")}
                            alt='card'
                          />
                        </div>
                      </div>
                    </div>
                    <div className='col-lg-6 text-left '>
                      <h4 className='font-weight-bold text-white py-3 coco'>Payment Details</h4>
                      {/* this is form felids  */}
                      <CardForms
                        onChangeForm={this.handleInputChange}
                        onFocusForm={this.handleInputFocus}
                        onChange={this.handelForm}
                        btnColor={this.state.btnColor}
                        carRun={this.state.carRun}
                        carBox={this.state.carBox}
                        text={this.state.text}
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default CreditCard;
