import React, { Component } from "react";

class NavBar extends Component {
  state = {
    activeBgC: " "
  };
  handelClose = () => {
    this.setState({
      sideWidth: "0"
    });
  };
  /*Description  when click on button of navbar to open it on small screens  */ 

  handelNav = () => {
    this.setState({
      sideWidth: "250px"
    });
  };
  componentDidMount() {
  /*Description  change navbar color when scroll and make it transparent when top = 0  */ 

    window.addEventListener("scroll", () => {
      if (window.scrollY > 2) {
        this.setState({ activeBgC: " add-this-class " });
      } else {
        this.setState({ activeBgC: "   " });
      }
    });
  }
  render() {
    return (
      <div>
        <nav className={"navbar navbar-expand-lg navbar-light fixed-top" + this.state.activeBgC}>
          <div className='container'>
            <div className='text-left'>
              <img
                className='logo'
                src={require("../../assets/images/c7b63492535aaaac5440a40e89d019e70e3e55b2.png")}
                alt=''
              />
              <a className='navbar-brand' href='/'>
                <span>Turbo </span>
                <span className='d-block brand-net  text-white'>Network</span>
              </a>
            </div>
            <button className='navbar-toggler border-0' type='button' onClick={this.handelNav}>
              <svg
                width='20px'
                height='16px'
                viewBox='0 0 20 16'
                version='1.1'
                xmlns='http://www.w3.org/2000/svg'>
                <g id='menu'>
                  <path
                    d='M18.718 2L1.28207 2C0.573828 2 0 1.55241 0 1.00002C0 0.447617 0.573828 0 1.28207 0L18.718 0C19.4262 0 20 0.447586 20 1.00002C20 1.55244 19.4262 2 18.718 2Z'
                    id='Path'
                    fill='#FFFFFF'
                    stroke='none'
                  />
                  <path
                    d='M11.7 2L1.30002 2C0.58186 2 0 1.55239 0 1C0 0.44761 0.58186 0 1.30002 0L11.7 0C12.4181 0 13 0.447579 13 1C13 1.55242 12.4181 2 11.7 2Z'
                    transform='translate(0 7)'
                    id='Path'
                    fill='#FFFFFF'
                    stroke='none'
                  />
                  <path
                    d='M18.718 2L1.28207 2C0.573828 2 0 1.55239 0 1C0 0.44761 0.573828 0 1.28207 0L18.718 0C19.4262 0 20 0.447579 20 1C20 1.55239 19.4262 2 18.718 2Z'
                    transform='translate(0 14)'
                    id='Path'
                    fill='#FFFFFF'
                    stroke='none'
                  />
                </g>
              </svg>
            </button>

            <div className='cma' id='navbarSupportedContent'>
              <ul className='navbar-nav ml-auto'>
                <li className='nav-item'>
                  <a className='nav-link' href='#features'>
                    Feature
                  </a>
                </li>
                <li className='nav-item'>
                  <a className='nav-link' href='#support'>
                    Support
                  </a>
                </li>
                <li className='nav-item'>
                  <a className='nav-link' href='#proven'>
                    Proven
                  </a>
                </li>
                <li className='nav-item'>
                  <a className='nav-link' href='#dashboard'>
                    Dashboard
                  </a>
                </li>
                <li className='nav-item'>
                  <a className='nav-link' href='#faq'>
                    FAQ
                  </a>
                </li>
                <li className='nav-item'>
                  <a className='nav-link' href='#contact'>
                    Contact
                  </a>
                </li>
                <li className='nav-item'>
                  <a className='nav-link dash-link' href='#contact'>
                    Dashboard
                  </a>
                </li>
              </ul>
            </div>
          </div>
        </nav>

        <div id='mySidenav' className='sidenav' style={{ width: this.state.sideWidth }}>
          <span  className='closebtn' onClick={this.handelClose}>
            &times;
          </span>
          <nav className={"navbar navbar-expand-lg navbar-light "}>
            <div className='w-100'>
              <div className='' id='navbarSupportedContent'>
                <ul className='navbar-nav ml-auto'>
                  <li className='nav-item'>
                    <a className='nav-link' href='#features'>
                      Feature
                    </a>
                  </li>
                  <li className='nav-item'>
                    <a className='nav-link' href='#support'>
                      Support
                    </a>
                  </li>
                  <li className='nav-item'>
                    <a className='nav-link' href='#proven'>
                      Proven
                    </a>
                  </li>
                  <li className='nav-item'>
                    <a className='nav-link' href='#dashboard'>
                      Dashboard
                    </a>
                  </li>
                  <li className='nav-item'>
                    <a className='nav-link' href='#faq'>
                      FAQ
                    </a>
                  </li>
                  <li className='nav-item'>
                    <a className='nav-link' href='#contact'>
                      Contact
                    </a>
                  </li>
                  <li className='nav-item'>
                    <a className='nav-link dash-link d-block text-center' href='#contact'>
                      Dashboard
                    </a>
                  </li>
                </ul>
              </div>
            </div>
          </nav>
        </div>
      </div>
    );
  }
}

export default NavBar;
